import { flatten } from "underscore";

export default {
  data() {
    return {
      q: "",
      focus: false,
      focusIndex: -1,
      open: false,
      searching: false,
      results: [],
    };
  },
  watch: {
    async q(q) {
      this.focusIndex = -1;
      if (!q) {
        this.searching = false;
        this.results = [];
        return;
      }
      this.searching = true;
      this.results = await this.$content(this.$i18n.locale, { deep: true })
        .sortBy("position", "asc")
        .only(["title", "slug", "category", "to", "meta_data"])
        .limit(12)
        .search(q)
        .fetch();

      let allResults = this.results;

      // Get all articles
      this.all = await this.$content(this.$i18n.locale, {
        deep: true,
      }).fetch();

      // Get data if data frontmatter is set
      let jsonResults = [];
      for (let index = 0; index < this.all.length; index++) {
        const article = this.all[index];
        if (article.data) {
          await fetch(article.data).then(async function (response) {
            let articleData = await response.json();
            let values = getValues(articleData);
            for (let index = 0; index < values.length; index++) {
              const val = values[index];
              // Flatten all values and search for query string
              if (typeof val == "string" && val.toLowerCase().search(q) > -1) {
                jsonResults.push(article);
                break;
              }
            }
          });
        }
      }

      jsonResults.forEach(function (jsonResult) {
        let present = allResults.find(function (result) {
          result.path == jsonResult.path;
        });
        if (!present) {
          allResults.push(jsonResult);
        }
      });

      this.searching = false;

      console.log(this.results);

      // Helper function for getting all values from object or list of objects
      function getValues(obj, collect) {
        collect = typeof collect == "undefined" ? [] : collect;
        if (typeof obj === "array") {
          obj.forEach((o) => {
            getValues(o, collect);
          });
        }
        if (typeof obj === "object") {
          for (var key in obj) {
            if (typeof obj[key] === "object") {
              getValues(obj[key], collect);
            } else {
              collect.push(obj[key]);
            }
          }
        }
        return collect;
      }
    },
  },
  mounted() {
    window.addEventListener("keyup", this.keyup);
  },
  beforeDestroy() {
    window.removeEventListener("keyup", this.keyup);
  },
  methods: {
    onFocus() {
      this.focus = true;
      this.$emit("focus", true);
    },
    onBlur() {
      this.focus = false;
      this.$emit("focus", false);
    },
    keyup(e) {
      if (e.key === "/") {
        this.$refs.search.focus();
      }
    },
    increment() {
      if (this.focusIndex < this.results.length - 1) {
        this.focusIndex++;
      }
    },
    decrement() {
      if (this.focusIndex >= 0) {
        this.focusIndex--;
      }
    },
    go() {
      if (this.results.length === 0) {
        return;
      }
      const result =
        this.focusIndex === -1
          ? this.results[0]
          : this.results[this.focusIndex];
      this.$router.push(this.localePath(result.to));
      // Unfocus the input and reset the query.
      this.$refs.search.blur();
      this.q = "";
    },
  },
};
